@import url(https://fonts.googleapis.com/css2?family=Old+Standard+TT&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* @import url('https://fonts.googleapis.com/css2?family=IM+Fell+English+SC&display=swap'); */
body,
html {
  height: 100%;
}
.bodycss {
  background-image: url(/static/media/47031332.f6a7063d.jpg);
  /* background-color:#010202; */
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}
.unav {
  border-bottom: 3px solid #f8f9fa;
  transition-duration: 0.5s;
}
.unav:hover {
  border-bottom: 2px solid rgb(0, 225, 255);
}
.container .input {
  border: 0;
  outline: none;
  color: #5e0000;
}
.search_wrap {
  width: 500px;
  margin: 38px auto;
}
.search_wrap .search_box {
  position: relative;
  width: 100%;
  height: 60px;
}
.search_wrap .search_box .input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 10px 20px;
  border-radius: 3px;
  font-size: 18px;
}
.search_wrap .search_box .btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 100%;
  background: #e63946;
  z-index: 1;
  cursor: pointer;
}
.search_wrap .search_box .btn:hover {
  background: #af242f;
}
.search_wrap .search_box .btn.btn_common .fas {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #fff;
  font-size: 20px;
}
.search_wrap.search_wrap_over .search_box .input {
  padding-right: 80px;
}
.search_wrap.search_wrap_over .search_box .input {
  border-radius: 50px;
}
.search_wrap.search_wrap_over .search_box .btn {
  right: 0px;
  border-radius: 50%;
}
.fontstyle {
  /* font-family: 'IM Fell English SC', serif; */
  font-family: 'Old Standard TT', serif;
}
.carddesign {
  background-color: rgba(255, 255, 255, 0.4);
}
.formround {
  border-radius: 3rem;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}

.contact {
    min-height: 100vh;
    width: 100vw;
    padding: 100px 50px 50px 50px;
    background: rgb(74, 129, 53);
    color: rgb(12, 8, 248);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  form {
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 15px;
    width: 300px;
    align-items: left;
    color: black;
    background: white;
  }
  
  input {
    border: none;
    border-bottom: 2px solid rgba(0, 0, 0, 0.6);
    color: black;
    outline: none;
  }
  
  input::-webkit-input-placeholder {
    color: rgba(0, 0, 0, 0.8);
    text-align: left;
  }
  
  input:-ms-input-placeholder {
    color: rgba(0, 0, 0, 0.8);
    text-align: left;
  }
  
  input::placeholder {
    color: rgba(0, 0, 0, 0.8);
    text-align: left;
  }
  
  input:hover {
    border-bottom: 2px solid rgba(0, 0, 0, 1);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  
  textarea {
    width: 250px;
    height: 250px;
    background: transparent;
    border: none;
    outline: none;
    color: black;
  }
  
  textarea::-webkit-input-placeholder {
    color: rgba(0, 0, 0, 0.8);
  }
  
  textarea:-ms-input-placeholder {
    color: rgba(0, 0, 0, 0.8);
  }
  
  textarea::placeholder {
    color: rgba(0, 0, 0, 0.8);
  }
  
  form button {
    padding: 7px 10px;
    width: 200px;
    background-color: rgb(0, 204, 153);
    background-image: linear-gradient(147deg, #990000 0%, #ff0000 74%);
    color: white;
    outline: none;
    border-radius: 17px;
    border: none;
    transition: 0.3s ease;
  }
  
  form button:hover {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  
  .contact_text a {
    text-decoration: none;
    color: white;
    cursor: pointer;
    font-weight: 600;
  }
.about {
    min-height: 100vh;
    width: 100vw;
    padding: 100px 50px 50px 50px;
    background: rgb(58, 55, 216);
    color: rgba(255, 255, 255, 0.8);
  }
  
  iframe {
    margin-left: 20px;
    margin-bottom: 20px;
    margin-top: 20px;
  }
  
  iframe:last-child {
    padding-right: 20px;
  }
  
  .videos {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    display: flex;
    overflow-x: scroll;
    width: 100vw;
    background-color: rgb(0, 204, 153);
    background-image: linear-gradient(147deg, #0cfce4 0%, #00f7ff 74%);
  }
  
  .videos::-webkit-scrollbar {
    height: 10px;
    outline: none;
  }
  
  .videos::-webkit-scrollbar-thumb {
    border-radius: 0;
    background: white;
  }
  
  @media (max-width: 500px) {
    .videos::-webkit-scrollbar {
      display: none;
    }
  }
