.about {
    min-height: 100vh;
    width: 100vw;
    padding: 100px 50px 50px 50px;
    background: rgb(58, 55, 216);
    color: rgba(255, 255, 255, 0.8);
  }
  
  iframe {
    margin-left: 20px;
    margin-bottom: 20px;
    margin-top: 20px;
  }
  
  iframe:last-child {
    padding-right: 20px;
  }
  
  .videos {
    transform: translateX(-50px);
    display: flex;
    overflow-x: scroll;
    width: 100vw;
    background-color: rgb(0, 204, 153);
    background-image: linear-gradient(147deg, #0cfce4 0%, #00f7ff 74%);
  }
  
  .videos::-webkit-scrollbar {
    height: 10px;
    outline: none;
  }
  
  .videos::-webkit-scrollbar-thumb {
    border-radius: 0;
    background: white;
  }
  
  @media (max-width: 500px) {
    .videos::-webkit-scrollbar {
      display: none;
    }
  }